import { Col, Container, Image, Row } from "react-bootstrap"
import Menu from "./Menu"
import healthHeaderImage from '../assets/img/health-header-image.jpg'
import hospital from '../assets/img/hospital.png'
import drug from '../assets/img/drug.png'
import pharmacy from '../assets/img/pharmacy.png'
import doctor from '../assets/img/doctor.png'
import {Link} from 'react-router-dom'
import { useEffect, useState } from "react"

const Header = () => {
    const [ActivePage, setActivePage] = useState("")

    const ActivePageChange = (page) => {
        setActivePage(page)
    } 

    useEffect(() => {
        let page = window.location.pathname
        setActivePage(page)
    }, [])
    
    return (
        <Container fluid className="px-0 position-relative d-flex justify-content-center border-bottom border-default border-w-2">
            <Image src={healthHeaderImage} className="w-100"/>
            <Container className="position-absolute top-0 bottom-0 d-flex align-items-center text-white px-0">
                <div>
                    <h1 className="m-0 big-title">Harena</h1>
                    <h2>Ny fahasalamana</h2>
                </div>
                <Row className="position-absolute bottom-0 w-100 mx-0">
                    <Col className={"mx-1 rounded-top px-0 btn-default-1 " + ((ActivePage === "/Health/Hospital" || ActivePage === "/Health") && "sub-menu-active")}>
                        <Link to="/Health/Hospital" className="text-decoration-none" onClick={() => ActivePageChange("/Health/Hospital")}><Menu icon={hospital} text={"CENTRE DE SOIN"} /></Link>
                    </Col>
                    <Col className={"mx-1 rounded-top px-0 btn-default-1 " + (ActivePage === "/Health/Pharmacy" && "sub-menu-active")}>
                        <Link to="/Health/Pharmacy" className="text-decoration-none" onClick={() => ActivePageChange("/Health/Pharmacy")}><Menu icon={pharmacy} text={"PHARMACIE"} /></Link>
                    </Col>
                    <Col className={"mx-1 rounded-top px-0 btn-default-1 " + (ActivePage === "/Health/Doctor" && "sub-menu-active")}>
                        <Link to="/Health/Doctor" className="text-decoration-none" onClick={() => ActivePageChange("/Health/Doctor")}><Menu icon={doctor} text={"DOCTEUR"} /></Link>
                    </Col>
                    <Col className={"mx-1 rounded-top px-0 btn-default-1 " + (ActivePage === "/Health/Drug" && "sub-menu-active")}>
                        <Link to="/Health/Drug" className="text-decoration-none" onClick={() => ActivePageChange("/Health/Drug")}><Menu icon={drug} text={"MEDICAMENT"} /></Link>
                    </Col>
                </Row>
            </Container>
        </Container>
    )
}

export default Header
