import { combineReducers } from 'redux'

const logInStore = (state = false, action) => {
    if(action.type === "logIn"){ 
        switch (action.value) {
            case "hide" : state = false
                break
            case "show" : state = true
                break
            default     : 
                break
        }
    }
    return state
}

const signInStore = (state = false, action) => {
    if(action.type === "signIn"){
        switch (action.value) {
            case "hide" : state = false
                break
            case "show" : state = true
                break
            default     : 
                break
        }
    }
    return state
}

const confirmSignInStore = (state = false, action) => {
    if(action.type === "confirmSignIn"){
        switch (action.value) {
            case "hide" : state = false
                break
            case "show" : state = true
                break
            default     : 
                break
        }
    }
    return state
}

const changeAvatarStore = (state = false, action) => {
    if(action.type === "changeAvatar"){
        switch (action.value) {
            case "hide" : state = false
                break
            case "show" : state = true
                break
            default     : 
                break
        }
    }
    return state
}

const activePageStore = (state = "", action) => {
    if(action.type === "activePage"){
        state = action.value
    }
    return state
}

const Store = combineReducers({
    logIn: logInStore,
    signIn: signInStore,
    confirmSignIn: confirmSignInStore,
    activePage: activePageStore,
    changeAvatar: changeAvatarStore,
})

export default Store
