import { Image } from "react-bootstrap"

const Menu = ({icon,text}) => {
    return (
        <div className={"menu py-2 d-flex flex-column align-items-center justify-content-center text-white"}>
            <div className="iconMenu"><Image src={icon} className="w-100"/></div>
            <p className="text-center small mb-0 mt-1 menuText pl-2 border">{text}</p>
        </div>
    )
}

export default Menu
