import {Navbar, Nav} from 'react-bootstrap'
import Logo from '../assets/img/diariko.png'
import User from './User'
import {Link} from 'react-router-dom'
import LogIn from './LogIn'
import SignIn from './SignIn'
import SearchBar from './SearchBar'
import ConfirmSingIn from './ConfirmSingIn'
import ChangeAvatar from './ChangeAvatar'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

const NavigBar = () => {

    useEffect(() => {
        let page = window.location.pathname.replace("/","")
        activePageHandleChange({type: "activePage", value: page})
        // eslint-disable-next-line
    }, [])

    const activePageHandleChange = useDispatch()

    const activePageState = useSelector(state => state.activePage)

    return (
        <Navbar bg="" variant="dark" expand="lg" fixed="top">
            <Navbar.Brand>
                <Link to="/" onClick={() => activePageHandleChange({type: "activePage", value: ""})}><img src={Logo} alt="Diariko"/></Link>
            </Navbar.Brand>
            <SearchBar container="w-25" bar="rounded-pill-right" icon="rounded-pill-left" placeholder="Recherche" />
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="w-100 d-flex justify-content-between mr-5">
                    <Link to="/" className={"nav-link " + (activePageState === "" && "active") } onClick={() => activePageHandleChange({type: "activePage", value: ""})}>ACCUEIL</Link>
                    <Link to="/Health" className={"nav-link " + (activePageState.includes("Health") && "active")} onClick={() => activePageHandleChange({type: "activePage", value: "Health"})}>SANTE</Link>
                    <Link to="/Study" className={"nav-link " + (activePageState === "Study" && "active")} onClick={() => activePageHandleChange({type: "activePage", value: "Study"})}>ETUDE</Link>
                    <Link to="/Work" className={"nav-link " + (activePageState === "Work" && "active")} onClick={() => activePageHandleChange({type: "activePage", value: "Work"})}>EMPLOI</Link>
                    <Link to="/Family" className={"nav-link " + (activePageState === "Family" && "active")} onClick={() => activePageHandleChange({type: "activePage", value: "Family"})}>FAMILLE</Link>
                </Nav>
                <User/>
            </Navbar.Collapse>
            <LogIn/>
            <SignIn/>
            <ConfirmSingIn/>
            <ChangeAvatar/>
        </Navbar>
    )
}

export default NavigBar
