import React from 'react'
import { Container } from 'react-bootstrap'
import PharmacyBg from '../assets/img/pharmacy-bg.jpg'
import PharmacyContainer from './PharmacyContainer'
import pharmacyLogo from '../assets/img/pharmacie-logo.png'
import greenLogo from '../assets/img/green-logo.png'
import PharmacyAnosizato from '../assets/img/pharmacie-anosizato.jpg'
import Definition from "./Definition"
import ImageDefinition from "./ImageDefinition"

const Pharmacy = () => {
    const text = `Le problème habituel de presque tout le monde, c’est surtout de trouver une pharmacie ouverte la nuit ou même le week-end. 
    Un véritable casse-tête surtout si vous êtes seul à la maison et vous ne vous sentez pas bien du tout. 
    La question qui se pose c’est : "comment trouver une pharmacie au plus vite, demander une livraison à tout moment ?" 
    Diariko c’est la solution à tous ces problèmes. Et oui, sur notre site internet il suffit d’allumer la connexion et vous 
    trouverez toutes les pharmacies de garde les plus proche de vous. Encore mieux, nous avons plusieurs médicaments disponibles sur notre 
    site car nous travaillons avec la majorité des pharmacies locales.`
    return (
        <>
            <Definition title="Pharmacie" text={text}/>
            <ImageDefinition image={PharmacyBg} placeholder="Pharmacie, Point de vente, Distributeur, etc..."/>
            <Container className="mt-5">
                <PharmacyContainer logo={pharmacyLogo} name="Pharmacie d'Anosizato" photo={PharmacyAnosizato} address="lot Lorem Anosizato Andrefana" tel="20 22 345 67" mail="address@mail.com" horaire="24h/24" garde={true} />
                <PharmacyContainer logo={greenLogo} name="Zoky Andry Mampifaly" photo={PharmacyAnosizato} address="lot Lorem Ambodin'Isotry" tel="20 22 345 67" mail="address@mail.com" horaire="24h/24" garde={false} />
                <PharmacyContainer logo={pharmacyLogo} name="Pharmacie 67ha" photo={PharmacyAnosizato} address="lot Lorem 67ha" tel="20 22 345 67" mail="address@mail.com" horaire="24h/24" garde={true} />
            </Container>
        </>
    )
}

export default Pharmacy
