import { Image } from "react-bootstrap"
import SearchBar from "./SearchBar"

const ImageDefinition = ({image, placeholder}) => {
    return (
        <div className="mt-5 position-relative">
            <Image src={image} className="w-100"/>
            <div className="position-absolute d-flex justify-content-center pb-5 bottom-0 w-100">
                <SearchBar container="w-50" placeholder={placeholder}/>
            </div>
        </div>
    )
}

export default ImageDefinition
