import { Container } from "react-bootstrap"
import DoctorBg from '../assets/img/doctor-bg.jpg'
import DoctorContainer from "./DoctorContainer"
import DrPhoto from '../assets/img/dr-photo.jpg'
import Definition from "./Definition"
import ImageDefinition from "./ImageDefinition"

const Doctor = () => {
    const text = `Bienvenue à vous chers visiteurs. 
    Vous recherchez un médecin à proximité de chez vous ? 
    Nous disposons d’une liste de médecins diplômés par l’état.  
    Vous trouverez ainsi des chirurgiens, des dentistes, des médecins généralistes etc. 
    Notre site affiche également les heures d’ouverture et de fermeture des cabinets médicaux 
    ainsi que les hôpitaux et les cliniques sur la grande île. Si vous souhaitez prendre rendez-vous, 
    nous affichons leurs contacts pour faciliter les échanges.`
    return (
        <>
            <Definition title="Docteur" text={text}/>
            <ImageDefinition image={DoctorBg} placeholder="Docteur, Infirmière, Sage-femme, etc..."/>
            <Container className="mt-5">
                <DoctorContainer name="Dr Rakoto Voahangy" photo={DrPhoto} speciality="Chirurgie" address="lot Lorem 67ha" tel="20 22 345 67" mail="address@mail.com" horaire="24h/24"/>
                <DoctorContainer name="Dr Rakoto Voahangy" photo={DrPhoto} speciality="Chirurgie" address="lot Lorem 67ha" tel="20 22 345 67" mail="address@mail.com" horaire="24h/24"/>
                <DoctorContainer name="Dr Rakoto Voahangy" photo={DrPhoto} speciality="Chirurgie" address="lot Lorem 67ha" tel="20 22 345 67" mail="address@mail.com" horaire="24h/24"/>
            </Container>
        </>          
    )
}

export default Doctor
