                        username: (name === "username")? "ne peut pas contenir d'espace" : state.username,
                        phoneNumber: (name === "phoneNumber")? "ne peut pas contenir d'espace" : state.phoneNumber,
                        password: (name === "password")? "ne peut pas contenir d'espace" : state.password,
                        address: (name === "address")? "ne peut pas contenir d'espace" : state.address,
