import { Form } from "react-bootstrap"

const Choose = ({data,selected,chooseHandleChange,name,status,addClass}) => {
    const handleChange = (e) => {
        chooseHandleChange(e)
    }

    const OPTION =  data.map((item,index) => {
        return <option key={index} value={item.id}>{item.name}</option>
    })
    return (
        (status === "error")?
        <Form.Control isInvalid as="select" name={name} value={selected} custom  className={"choose " + addClass} onChange={handleChange}>
            {OPTION}
        </Form.Control>:
        <Form.Control as="select" name={name} value={selected} custom  className={"choose " + addClass} onChange={handleChange}>
            {OPTION}
        </Form.Control>
    )
}

export default Choose
