import { useEffect, useRef, useState } from "react"
import { Button, Dropdown, Form, Image, Modal } from "react-bootstrap"
import CameraTake from 'react-html5-camera-photo'
import { Camera, Upload, CameraVideo } from 'react-bootstrap-icons'
import AvatarEditor from 'react-avatar-editor'
import axios from "axios"
import config from "../Config"
import { useDispatch, useSelector } from "react-redux"

const ChangeAvatar = () => {
    const AcceptedFile = "image/jpeg, image/png"
    const [Avatar, setAvatar] = useState("")
    const [CameraStatus, setCameraStatus] = useState(false)
    const [Scale, setScale] = useState(1)
    const [Editor, setEditor] = useState("")
    const [Ok, setOk] = useState(false)
    const [File, setFile] = useState()
    const InputFile = useRef()
    axios.defaults.baseURL = config.baseUrl

    const initialize = () => {
        setAvatar("")
        setCameraStatus(false)
        setScale(1)
        setEditor("")
        setOk(false)
    }

    const ImportFile = () => InputFile.current.click()

    const FileHandleChange = (e) => {
        setCameraStatus(false)
        setOk(true)
        if (e.target.files && e.target.files[0]) {
            let reader = new FileReader()
            reader.onload = (event) => {
                setAvatar(event.target.result)
            }
            reader.readAsDataURL(e.target.files[0])
        }
    }

    const TakePhoto = () => {
        setCameraStatus(true)
    }

    const TakePhotoDone = (photo) => {
        setCameraStatus(false)
        setAvatar(photo)
        setOk(true)
    }

    const ScaleHandleChange = (e) => {
        setScale(e.target.value)
    }

    const setEditorRef = (editor) => setEditor(editor)

    const AvatarAccepted = () => {
        fetch(Editor.getImage().toDataURL()).then(res => res.blob()).then(blob => {
            setAvatar(window.URL.createObjectURL(blob))
            blob.name = "image.png"
            blob.lastModified = new Date().getTime()
            setFile(blob)
        })
        setScale(1)
        setOk(false)
    }

    const Termier = () => {
        const formData = new FormData()
        formData.append('Photo', File)
        axios.post('/diariko/user/setPhoto', formData, { headers: { xsrf: localStorage.getItem("diarikoXsrf")} , withCredentials: true }).then(res => {
            if(res.data.diarikoXsrf){
                localStorage.setItem("diarikoXsrf",res.data.diarikoXsrf)
            }
        })
    }

    const handleClose = useDispatch()

    const changeAvatarState = useSelector(state => state.changeAvatar)

    useEffect(() => {
        (changeAvatarState) && initialize()
        // eslint-disable-next-line
    }, [changeAvatarState])

    return (
        <Modal show={changeAvatarState} onHide={() => handleClose({type: "changeAvatar", value: "hide"})} backdrop="static" keyboard={false} centered>
            <Modal.Header closeButton>
                <Modal.Title>Changez votre Profil</Modal.Title>
            </Modal.Header>
            <Modal.Body className="pb-0">
                <Form className="d-flex flex-column align-items-center">
                    <Form.Group className="avatarChoose rounded-circle bg-dark d-flex flex-column justify-content-center position-relative">
                        { Ok? <AvatarEditor ref={setEditorRef} image={Avatar} width={370} height={370} border={0} color={[255, 255, 255, 0.6]} scale={parseFloat(Scale)} rotate={0} borderRadius={240}/>:<Image src={Avatar} roundedCircle/>}
                        <Dropdown className="bg-white text-dark border rounded-circle cam d-flex justify-content-center align-items-center position-absolute">
                            <Dropdown.Toggle variant="">
                                <Camera size={30}/>
                            </Dropdown.Toggle>
                            <Dropdown.Menu className="mt-2">
                                <Dropdown.Item className="text-secondary" onClick={ImportFile}><Upload className="mr-2"/> Importer</Dropdown.Item>
                                <Dropdown.Item className="text-secondary" onClick={TakePhoto}><CameraVideo className="mr-2"/> Camera</Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Form.Group>
                    <Form.Group className={"w-50 " + (!Ok && "hide")}>
                        <Form.Control type="range" custom value={Scale} min={1} max={2} step={0.1} onChange={ScaleHandleChange}/>
                    </Form.Group>    
                    <Form.File className="d-none" ref={InputFile} accept={AcceptedFile} custom onChange={FileHandleChange}/>
                </Form>
                {CameraStatus && <CameraTake onTakePhoto={TakePhotoDone} isMaxResolution={true} isFullscreen={true}/>}
            </Modal.Body>
            <Modal.Footer className="d-flex justify-content-center">
                { Ok && <Button variant="default" className="w-25 py-2" onClick={AvatarAccepted}>OK</Button>}
                { (!Ok && Avatar !== "") && <Button variant="default" className="w-25 py-2" onClick={Termier}>Términer</Button>}
                <Button variant="dark" className="w-25 py-2" onClick={() => handleClose({type: "changeAvatar", value: "hide"})}>Ignorer</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default ChangeAvatar
