import { Container } from "react-bootstrap"
import HospitalBg from '../assets/img/hospital-bg.jpg'
import Definition from './Definition'
import HospitalContent from "./HospitalContent"
import ImageDefinition from './ImageDefinition'
import hospitalContentBg from '../assets/img/hospital-content-bg.jpg'

const Hospital = () => {
    const text = `C’est quoi un hôpital pour Diariko ? C’est un établissement où l’on prend soin des personnes 
    malades ou vulnérables. En cas de maladie, surtout en période de pandémie la question qui se pose c’est : 
    quel hôpital est ouvert ? Grâce à notre site internet vous avez accès à cette information en quelques clics 
    seulement. Non seulement, vous pouvez retrouver les hôpitaux mais également les cliniques et les CSB à 
    proximité de chez vous. Il ne vous reste plus qu’à prendre contact avec eux directement.`
    return (
        <>
            <Definition title="CENTRE DE SOIN" text={text}/>
            <ImageDefinition image={HospitalBg} placeholder="Hopital, Clinique, CSB, Cabinet, etc..."/>
            <Container className="mt-5">
                <HospitalContent name="HJRA AMPEFILOHA" photo={hospitalContentBg} address="lot Lorem Ampefiloha Antananarivo" tel="20 22 345 67" mail="address@mail.com" ambulance="20 22 345 67"/>
                <HospitalContent name="HJRA AMPEFILOHA" photo={hospitalContentBg} address="lot Lorem Ampefiloha Antananarivo" tel="20 22 345 67" mail="address@mail.com" ambulance="20 22 345 67"/>
                <HospitalContent name="HJRA AMPEFILOHA" photo={hospitalContentBg} address="lot Lorem Ampefiloha Antananarivo" tel="20 22 345 67" mail="address@mail.com" ambulance="20 22 345 67"/>
            </Container>
         </>       
    )
}

export default Hospital
