import { Image } from "react-bootstrap"
import { useDispatch } from "react-redux"
import { Link } from "react-router-dom"

const ImageCard = ({image,icon,title,text,xpos,page}) => {
    const activePageHandleChange = useDispatch()

    return (
        <div className={"imageCard box-shadow position-relative d-flex px-3 pt-5 pb-3 my-4 justify-content-" + xpos} style={{backgroundImage:"url(" + image + ")"}}>
            <div className="position-absolute bg-white rounded-circle p-2 box-shadow iconService">
                <Image src={icon} roundedCircle className="bg-default p-1"/>
            </div>
            <div className="w-50">
                <h4 className="title">{title}</h4>
                <p className="text-default imageCardText">{text}</p>
                <Link className="btn btn-default-1 rounded-0 plus px-3" to={"/" + page} onClick={() => activePageHandleChange({type: "activePage", value: page})}>ALLEZ VERS</Link>
            </div>
        </div>
    )
}

export default ImageCard
