import UserImage from '../assets/img/user-40.png'
import {Dropdown} from 'react-bootstrap'
import { BoxArrowInRight, PersonPlus, Gear} from 'react-bootstrap-icons'
import axios from 'axios'
import config from '../Config'
import { useDispatch } from 'react-redux'
import { useState } from 'react'

const User = () => {
    axios.defaults.baseURL = config.baseUrl

    const [buffer, setbuffer] = useState("")

    const ShowLogIN = useDispatch()
    const ShowSignIN = useDispatch()

    // axios.post("/diariko/user/getPhoto", { headers: { xsrf: localStorage.getItem("diarikoXsrf")} , withCredentials: true }).then(res => {
    //     if(res.data.diarikoXsrf !== ""){
    //         localStorage.setItem("diarikoXsrf",res.data.diarikoXsrf)
    //     }
    //     if(res.data.buffer !== ""){
    //         setbuffer(res.data.buffer)
    //     }
    // })

    axios.post("/diariko/test", {},{ withCredentials: true }).then(res => {
        console.log(res.data)
    })

    return (
        <div>
            <Dropdown>
                <Dropdown.Toggle variant="">
                    <img src={ (buffer === "")? UserImage : 'data:image/jpeg;base64,' + buffer} className="dropdown-toggle border border-default border-w-2 rounded-circle user-small-avatar" alt="user" />
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item className="text-secondary" onClick={() => ShowSignIN({type: "signIn", value: "show"})}><PersonPlus className="mr-2"/> S'inscrire</Dropdown.Item>
                    <Dropdown.Item className="text-secondary" onClick={() => ShowLogIN({type: "logIn", value: "show"})}><BoxArrowInRight className="mr-2"/> Se connecter</Dropdown.Item>
                    <Dropdown.Divider/>
                    <Dropdown.Item href="#" className="text-secondary"><Gear className="mr-2"/> Paramètres</Dropdown.Item>
                    <Dropdown.Item className="text-secondary" onClick={""}> Nous aider à améliorer Diariko </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        </div>
    )
}

export default User
