import { Col, Container, Row } from "react-bootstrap"
import Story from "./Story"
import StoryContent from "./StoryContent"

const Body = ({children}) => {
    return (
        <Container className="pt-5 px-0" >
            <Row className="mx-0">
                <Col md={9} className="pl-0">
                    {children}
                </Col>
                <Col md={3} className="px-0">
                    <Story title="STORY">
                        <StoryContent title="CORONA VIRUS" date="10 March,2021" text="Lorem Ipsum is simply dummy text of the printing and typesetting industry."/>
                        <StoryContent title="PALUDISME" date="04 Feb,2021" text="Lorem Ipsum is simply dummy text of the printing and typesetting industry."/>
                        <StoryContent title="VACCIN" date="25 Jan,2021" text="Lorem Ipsum is simply dummy text of the printing and typesetting industry."/>
                    </Story>
                </Col>
            </Row>
        </Container>
    )
}

export default Body
