import {useEffect, useState } from "react"
import { Modal, Button, Form} from "react-bootstrap"
import {Link} from 'react-router-dom'
import axios from "axios"
import { Eye, EyeSlash } from "react-bootstrap-icons"
import config from "../Config"
import { useDispatch, useSelector } from "react-redux"

const LogIn = () => {
    axios.defaults.baseURL = config.baseUrl
    const [Identifiant, setIdentifiant] = useState("")
    const [PasswordStatus, setPasswordStatus] = useState("hide")
    const logInData = {
        identifiant: "",
        password: ""
    }

    const [LogInData, setLogInData] = useState(logInData)

    const passwordStatusHandleChange = () => {
        (PasswordStatus === "hide")? setPasswordStatus("show") : setPasswordStatus("hide")
    }

    const handleChange = (e) => {
        const {name,value} = e.target
        setLogInData({...LogInData, [name]: value})
    }

    const {identifiant,password} = LogInData

    useEffect(() => {
        axios.post('/diariko/identifiant',{identifiant: identifiant}).then(res => {
            (res.data.length > 0)? setIdentifiant("valid") : setIdentifiant("")
        })
    }, [identifiant])

    const Connexion = () => {
        // axios.post('/diariko/logIn',{identifiant: identifiant, password:password}).then(res => {
        //     if(res.data.length > 0){
        //     }
        // })
        axios.post('/diariko/user/auth',"",{ headers: { xsrf: localStorage.getItem("diarikoXsrf")} , withCredentials: true }).then(res => {
            localStorage.setItem("diarikoXsrf",JSON.stringify(res.data))
        })
    }

    const ShowSignIN = () => {
        handleClose({type: "logIn", value: "hide"})
        handleShowSignIn({type: "signIn", value: "show"})
    }

    const handleClose = useDispatch()
    const handleShowSignIn = useDispatch()

    const logInState = useSelector(state => state.logIn)

    return (
        <Modal show={logInState} onHide={() => handleClose({type: "logIn", value: "hide"})} backdrop="static" keyboard={false} centered>
            <Modal.Header closeButton>
                <Modal.Title>Connectez-vous</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Group className="mt-3 mx-3">
                        {(Identifiant === "valid")?<Form.Control isValid placeholder="Nom d'utilisateur ou numero téléphone" name="identifiant" value={identifiant} onChange={handleChange} className="py-4"/>:<Form.Control placeholder="Nom d'utilisateur ou numero téléphone" name="identifiant" value={identifiant} onChange={handleChange} className="py-4"/>}
                    </Form.Group>
                    <Form.Group className="my-4 mx-3 position-relative">
                        <Form.Control type={(PasswordStatus === "hide")? "password" : "text"} placeholder="Mot de passe" name="password" value={password} onChange={handleChange} className="py-4"/>
                        <div className="position-absolute top-0 right-0 px-3 h-100 d-flex align-items-center">
                            {(PasswordStatus === "hide")?<Eye className="cursor-pointer" onClick={passwordStatusHandleChange}/> : <EyeSlash className="cursor-pointer"  onClick={passwordStatusHandleChange}/>}
                        </div>
                    </Form.Group>
                    <Form.Group className="d-flex justify-content-center">
                        <Button variant="default" onClick={Connexion} className="w-50 py-2">Se connecter</Button>
                    </Form.Group>
                    <Form.Group className="text-center">
                        <Link>Mot de passe oublié ?</Link>
                    </Form.Group>
                </Form>
            </Modal.Body>
            <Modal.Footer className="d-flex justify-content-center">
                <Button variant="primary" onClick={ShowSignIN} className="w-50 py-2">Créer un compte</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default LogIn
