import { Carousel } from "react-bootstrap"
import HealthImage from '../assets/img/health.jpg'
import StudyImage from '../assets/img/study.jpg'
import WorkImage from '../assets/img/work.jpg'
import FamilyImage from '../assets/img/family.jpg'
import CarouselContent from "./CarouselContent"
import { ChevronRight,ChevronLeft } from 'react-bootstrap-icons'

const CarouselSlide = () => {
    const health_text = `Conseils santé, nutrition, bien-être, développement personnel et bien plus encore! 
    Un lieu de partage ou chacun peut s’informer pour améliorer sa santé, son bien-être et trouver quelques 
    astuces pour améliorer facilement son quotidien. Il est aussi possible de faire un diagnostic soi-même 
    en passant nos tests santé. `

    const study_text = `Le challenge de Diariko c’est de publier régulièrement des cours, des articles et des 
    interviews liés à l’univers du web. Avec le coronavirus et le confinement mondial, c’est le retour des cours 
    à distance des établissements d’enseignement supérieur et les lycées. Chez Diariko, on remplace les cours 
    traditionnel par des cours filmé ou des vidéos en direct.`
    
    const work_text = `Nous proposons ici des offres d’emploi pour faciliter le recrutement mais pas seulement. 
    Vous trouverez des conseils en ressources humaines afin d’adapter vos CV aux nouvelles tendances du marché 
    de l’emploi. On vous donnera également des conseils sur les formations à suivre en cas d’une reconversion 
    professionnelle.`

    const family_text = `Enfant adopté ou simplement à la recherche de plus d’information sur les membres de ta 
    famille ? Vous trouverez des informations sur toutes les familles Malgaches inscrites sur notre site internet. 
    Chaque famille possède des secrets donc notre site n’est pas à interpréter dans sa globalité.`

    return (
        <Carousel indicators={false} nextIcon={<ChevronRight size="30"/>} prevIcon={<ChevronLeft size="30"/>}>
            <Carousel.Item>
                <CarouselContent image={HealthImage} alt="Health" title="Parcours Sanitaire" text={health_text}/>
            </Carousel.Item>
            <Carousel.Item>
                <CarouselContent image={StudyImage} alt="Study" title="Parcours Scolaire" text={study_text}/>
            </Carousel.Item>
            <Carousel.Item>
                <CarouselContent image={WorkImage} alt="Work" title="Parcours Professionnel" text={work_text}/>
            </Carousel.Item>
            <Carousel.Item>
                <CarouselContent image={FamilyImage} alt="Family" title="Parcours Familiale" text={family_text}/>
            </Carousel.Item>
        </Carousel>
    )
}

export default CarouselSlide
