import { Container } from "react-bootstrap"
import DrugBg from '../assets/img/drug-bg.jpg'
import DrugContainer from "./DrugContainer"
import Definition from "./Definition"
import ImageDefinition from "./ImageDefinition"

const Drug = () => {
    const text = `Avant d’avaler un médicament, il est important de connaître les effets d’un tel ou tel. 
    Vous vous posez sans doute la question suivante : qu’es ce qu'un médicament ? Alors, un médicament c’est aussi bien 
    un remède qu’un poison. Et oui, il est possible de mourir en avalant un comprimé car une mauvaise posologie peut 
    être fatale pour votre corps. Notre conseil, c’est d’éviter de jouer les médecins et d’aller consulter. En effet, 
    les prescriptions médicales restent la meilleure option dans tous les cas. Notre site affiche également les remèdes 
    ancestraux ou plus connu comme étant des astuces de grand-mère.`

    return (
        <>
            <Definition title="Medicament" text={text}/>
            <ImageDefinition image={DrugBg} placeholder="Medicament, Complément alimentaire, Phytothérapie, etc..."/>
            <Container className="mt-5">
                <DrugContainer type="Medicament" typeClass="bg-danger text-white w-50" name="Paracetamol" store="Pharmacie 67ha"/>
                <DrugContainer type="Complément alimentaire" typeClass="bg-warning text-white w-50" name="Paracetamol" store="Zoky Andry"/>
                <DrugContainer type="Phytothérapie" typeClass="bg-success text-white w-50" name="Paracetamol" store="Zoky Andry"/>
            </Container>
        </>
                
    )
}

export default Drug
