import { useDispatch } from 'react-redux'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import Body from './components/Body'
import Doctor from './components/Doctor'
import Drug from './components/Drug'
import Header from "./components/Header"
import Hospital from './components/Hospital'
import Pharmacy from './components/Pharmacy'

const Health = () => {
    const activePageHandleChange = useDispatch()
    activePageHandleChange({type: "activePage", value: "Health"})
    return (
        <Router>
            <Header/>
            <Body>
                <Switch>    
                    <Route exact path="/Health" component={Hospital}/>     
                    <Route path="/Health/Hospital" component={Hospital}/>     
                    <Route path="/Health/Drug" component={Drug}/>     
                    <Route path="/Health/Pharmacy" component={Pharmacy}/>     
                    <Route path="/Health/Doctor" component={Doctor}/>  
                    {/* <Redirect from="/" to="/accueil" />    */}
                </Switch>
            </Body>
        </Router>
    )
}

export default Health
