import { useEffect, useState } from "react"
import { Button, Form, Modal, Spinner } from "react-bootstrap"
import firebase from './firebase'
import 'firebase/auth'
import axios from "axios"
import config from "../Config"
import { useDispatch, useSelector } from "react-redux"

const ConfirmSingIn = () => {
    const [ConfirmCode, setConfirmCode] = useState({value:"", isValid:false, status:"", message:"empty", error:"hide"})
    const [FormStatus, setFormStatus] = useState("display-none")
    const [SpinnerStatus, setSpinnerStatus] = useState("")
    const [Transparent, setTransparent] = useState("transparent")
    axios.defaults.baseURL = config.baseUrl

    let PhoneNumber = sessionStorage.getItem("diarikoPhoneNumber")
    let dialCode = sessionStorage.getItem("diarikoDialCode")
    const Recaptcha = () => {
        window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container', {
            'size': 'invisible',
            'callback': (response) => {
              sendConfirmCode()
            }
          })
    }

    const [ConfirmationResult, setConfirmationResult] = useState()

    const sendConfirmCode = () => {
        Recaptcha()
        const appVerifier = window.recaptchaVerifier
        firebase.auth().signInWithPhoneNumber(dialCode + PhoneNumber, appVerifier).then((confirmationResult) => {
            window.confirmationResult = confirmationResult
            setConfirmationResult(confirmationResult)
            console.log("SMS sent")
            setSpinnerStatus("display-none")
            setFormStatus("")
            setTransparent("")
        }).catch((error) => {
            console.log("SMS not sent")
        })
    }

    const ConfirmCodeHandleChange = (e) => {
        setConfirmCode({value:e.target.value, isValid:false, status:"", message:"empty", error:"hide"})
    }

    useEffect(() => {
        (ConfirmCode.value.length >= 6) && setConfirmCode(state => ({value:state.value, isValid:true, status:"", message:"empty", error:"hide"}))
    }, [ConfirmCode.value])

    const ConfirmCodeForSingIn = (e) => {
        if(ConfirmCode.isValid){
            const code = ConfirmCode.value
            ConfirmationResult.confirm(code).then((result) => {
                axios.post('/diariko/user/activate',{condition:PhoneNumber},{withCredentials: true }).then(res => { 
                    if(res.data.diarikoXsrf){
                        sessionStorage.removeItem("diarikoPhoneNumber")
                        sessionStorage.removeItem("diarikoDialCode")
                        localStorage.setItem("diarikoXsrf",JSON.stringify(res.data.diarikoXsrf))
                        console.log(res.data.diarikoXsrf)
                        handleClose({type: "confirmSignIn", value: "hide"})
                        ShowChangeAvatar({type: "changeAvatar", value: "show"})
                    }
                }) 
            }).catch((error) => {
                console.log("not ok")
                setConfirmCode(state => ({value:state.value, isValid:state.isValid, status:"error", message:"Code invalide", error:"show"}))
            })
        }else if(ConfirmCode.value === ""){
            setConfirmCode(state => ({value:state.value, isValid:state.isValid, status:"error", message:"empty", error:"hide"}))
        }else setConfirmCode(state => ({value:state.value, isValid:state.isValid, status:"error", message:"Le code doit être superieur à 6", error:"show"}))
    }

    const handleClose = useDispatch()

    
    const ShowChangeAvatar = useDispatch()
    
    const confirmSignInState = useSelector(state => state.confirmSignIn)
    
    useEffect(() => {
        (confirmSignInState) && sendConfirmCode()
        // eslint-disable-next-line
    }, [confirmSignInState])

    return (
        <Modal show={confirmSignInState} onHide={() => handleClose({type: "confirmSignIn", value: "hide"})} backdrop="static" keyboard={false} centered className={Transparent}>
            <Modal.Header closeButton className={FormStatus}>
                <Modal.Title>Confirmez l'inscription</Modal.Title>
            </Modal.Header>
            <Modal.Body className="pb-0 d-flex justify-content-center">
                <Spinner animation="border" variant="default" className={"spin " + SpinnerStatus}/>
                <div id="recaptcha-container"></div>
                <Form className={FormStatus}>
                    <Form.Group>
                        <Form.Label>Un message contenant un code de validation a été envoyer au numéro {PhoneNumber}</Form.Label>
                    </Form.Group>
                    <Form.Group>
                        <Form.Control placeholder="Entrer le code ici" className={"py-4 " + ConfirmCode.status} value={ConfirmCode.value} onChange={ConfirmCodeHandleChange}/>
                        <Form.Text className={"text-danger mb-3 " + ConfirmCode.error}>
                            {ConfirmCode.message}
                        </Form.Text>
                    </Form.Group>
                </Form>
            </Modal.Body>
            <Modal.Footer className={"d-flex justify-content-center " + FormStatus}>
                <Button variant="default" className="w-25 py-2" onClick={ConfirmCodeForSingIn}>Confirmer</Button>
            </Modal.Footer>
        </Modal>
    )
}

export default ConfirmSingIn
