import { Button, Col, Container, Row } from "react-bootstrap"
import { EnvelopeFill, GeoAltFill, TelephoneFill } from "react-bootstrap-icons"
import hospitalContentBg from '../assets/img/hospital-content-bg-1.png'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faAmbulance } from "@fortawesome/free-solid-svg-icons"

const HospitalContent = ({name,photo,address,tel,mail,ambulance}) => {
    return (
        <div className=" box-shadow mb-5" style={{backgroundImage: "url(" + hospitalContentBg + ")", backgroundRepeat: "no-repeat", backgroundSize: "cover"}}>
            <Container fluid>
                <Row className="border-bottom border-w-2 border-dark" style={{backgroundImage: "url(" + photo + ")", backgroundSize: "cover"}}>
                    <Col><h1 className="text-center py-2">{name}</h1></Col>
                </Row>
                <Row className="pt-4 pb-3">
                    <Col className="text-white pl-5">
                        <p className="mb-0 d-flex align-items-center"><h5 className="mr-3 mb-0"><GeoAltFill /></h5> {address}</p>
                        <p className="mb-0 mt-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><TelephoneFill/></h5> {tel}</p>
                        <p className="mb-0 mt-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><EnvelopeFill/></h5> {mail}</p>
                        <p className="mb-0 mt-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><FontAwesomeIcon icon={faAmbulance}/>  </h5> {ambulance}</p>
                    </Col>
                    <Col>
                        <h4>Lorem Ipsum</h4>
                        <p className="mb-0">Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        Lorem Ipsum has been the industry's standard dummy text ever since the
                        1500s, when an unknown printer took a galley of type and scrambled it to
                        make a type specimen book.</p>
                        <div className="d-flex justify-content-end">
                            <Button variant="primary">VOIR LA PAGE</Button>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default HospitalContent
