import { Button, Col, Container, Image, Row } from "react-bootstrap"
import { ClockFill, EnvelopeFill, GeoAltFill, TelephoneFill } from "react-bootstrap-icons"

const PharmacyContainer = ({logo,name,photo,address,tel,mail,horaire,garde}) => {
    return (
        <div className=" box-shadow mb-5">
            <Container fluid style={{backgroundImage: "url(" + photo + ")"}} className="pharmacy-container px-0">
                <Container fluid className="pharmacy-sub-container py-4">
                    <Row className="d-flex align-items-center mb-3 px-3 bg-dark text-white py-1 w-75">
                        <Image src={logo} />
                        <h3 className="ml-3 mb-0">{name}</h3>
                    </Row>
                    <Row>
                        <Col className="ml-4 my-3 pharmacy-info rounded p-0">
                            <p className="mb-0 px-3 py-3 d-flex align-items-center border-bottom"><h5 className="mr-3 mb-0"><GeoAltFill /></h5> {address}</p>
                            <p className="mb-0 px-3 py-3 d-flex align-items-center border-bottom"><h5 className="mr-3 mb-0"><TelephoneFill/></h5> {tel}</p>
                            <p className="mb-0 px-3 py-3 d-flex align-items-center border-bottom"><h5 className="mr-3 mb-0"><EnvelopeFill/></h5> {mail}</p>
                            <p className="mb-0 px-3 py-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><ClockFill/></h5> {horaire}</p>
                        </Col>
                        <Col></Col>
                    </Row>
                </Container>
            </Container>
            <Container className="pb-4 pt-1">
                <Row>
                    <Col>
                        <p className="mb-0 mt-4 d-flex align-items-center ml-4"><h5 className="mr-2 mb-0">Pharmacie de Garde:</h5> <span className={garde? "text-success":"text-danger"}>{garde? "OUI":"NON"}</span></p>
                    </Col>
                    <Col className="d-flex justify-content-end">
                        <Button variant="primary" className="mt-3">VOIR LA PAGE</Button>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default PharmacyContainer
