import { Button, Col, Container, Image, Row } from "react-bootstrap"
import { Chat, ClockFill, EnvelopeFill, GeoAltFill, PhoneFill, PlusSquare } from "react-bootstrap-icons"
import doctorContentBg from '../assets/img/doctor-content-bg.png'

const DoctorContainer = ({name,photo,speciality,address,tel,mail,horaire}) => {
    return (
        <div className=" box-shadow p-3 mb-5" style={{backgroundImage: "url(" + doctorContentBg + ")",backgroundRepeat: "no-repeat", backgroundSize: "cover", backgroundPosition: "top"}}>
            <Container fluid>
                <Row className="my-3">
                    <Col className="position-relative rounded pt-4 pb-5">
                        <div className="d-flex justify-content-center mb-5">
                            <Image src={photo} className="dr-photo rounded-circle border border-w-2 border-default"/>   
                        </div>
                        <div className="position-absolute left-0 right-0 bottom-0">
                            <h3 className="text-center">{name}</h3>
                            <h4 className="bg-dark py-4 text-white text-center mb-4">Spécialité: {speciality}</h4>
                        </div>
                            
                    </Col>
                    <Col className="pt-4 pl-5">
                        <div className="text-white">
                            <p className="mb-0 d-flex align-items-center"><h5 className="mr-3 mb-0"><GeoAltFill /></h5> {address}</p>
                            <p className="mb-0 mt-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><PhoneFill/></h5> {tel}</p>
                            <p className="mb-0 mt-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><EnvelopeFill/></h5> {mail}</p>
                            <p className="mb-4 mt-3 d-flex align-items-center"><h5 className="mr-3 mb-0"><ClockFill/></h5> {horaire}</p>
                        </div>
                        <Row className="my-4">
                            <Col>
                                <Button variant="dark"><PlusSquare className="mr-2 mb-1"/>S'abonner</Button>
                            </Col>
                            <Col>
                                <Button variant="dark"><Chat className="mr-1 mb-1"/> Message</Button>
                            </Col>
                        </Row>
                        <Row>
                            <Col className="text-white text-justify">
                                <p className="mb-0">Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                                Lorem Ipsum has been the industry's standard dummy text ever since the
                                1500s.</p>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default DoctorContainer
