import { Button } from "react-bootstrap"
import { Calendar3 } from "react-bootstrap-icons"

const StoryContent = ({title,date,text}) => {
    return (
        <div className="mx-2 pb-3 mb-2 border-bottom border-dark">
            <div className="d-flex justify-content-between align-items-center mb-2">
                <h5 className="mb-0">{title}</h5>
                <small><Calendar3 className="mr-1"/>{date}</small>
            </div>
            <small>{text}</small>
            <Button variant={"info"} className="mt-3">PLUS D'INFO</Button>
        </div>
    )
}

export default StoryContent
