import { Button, Col, Container, Row } from "react-bootstrap"
import { ChevronDoubleDown, Play } from "react-bootstrap-icons"
import { Link } from "react-router-dom"
import CarouselSlide from "./CarouselSlide"
import ImageCard from "./ImageCard"
import health from '../assets/img/sante.jpg'
import healthIcon from '../assets/img/healthIcon.png'
import study from '../assets/img/etude.jpg'
import studyIcon from '../assets/img/studyIcon.png'
import work from '../assets/img/emploi.jpg'
import workIcon from '../assets/img/workIcon.png'
import family from '../assets/img/famille.jpg'
import familyIcon from '../assets/img/familyIcon.png'
import { useDispatch } from "react-redux"

const HomeNotConnected = () => {
    const ShowSignIN = useDispatch()

    const ShowLogIN = useDispatch()

    const presentation_text = `Le projet Diariko est une révolution dans le domaine digitale et résau sociaux professionel.
     Non seulement notre site internet a pour but de faciliter la vie quotidienne mais elle pourra 
     bientôt aider le gouvernement à analyser des statistiques nationaux. Il sera possible de voir les membres de 
     famille et de constater rapidement la croissance en termes d’individu. Trouver un emploi ne sera plus dificile comme avant
     car notre site pourra certifier le sérieux de chaque candidat. Et oui, avoir la certification Diariko c’est encore mieux 
     qu’un diplôme universitaire, car on se base sur la compétence, le progrès personnel et surtout l'authenticité des informations.`

     const health_text = `Dans cette section vous trouverez tout ce dont vous avez besoin pour rester
     en bonne santé tout au long de l’année. Et cela, même en période de pandémie. `
 
     const study_text = `Cet onglet est réservé exclusivement aux étudiants et aux professeurs qui souhaitent
      interagir en temps réel.`
     
     const work_text = `Dans cet onglet vous retrouverez plusieurs CV et divers offres d’emploi. 
     Cette section reste l’endroit idéal pour les professionnels à la recherche d’un emploi ou d’une 
     éventuelle collaboration.`
 
     const family_text = `Dans cet onglet vous retrouverez les membres de votre famille inscrit sur notre site. 
     C’est l’occasion de constituer un arbre généalogique et rester en contact avec ceux qui vivent loin.`
    
    return (
        <Container fluid className="position-relative px-0">
            <Container fluid className="position-relative p-0">
                <CarouselSlide/>
                <Row className="position-absolute btnBar w-100 d-flex justify-content-center m-0 option">
                    <Col className="d-flex justify-content-end">
                        <Button variant="default" className="py-3" onClick={() => ShowSignIN({type: "signIn", value: "show"})}>S'inscrire</Button>
                    </Col>
                    <Col>
                        <Button variant="default" className="py-3" onClick={() => ShowLogIN({type: "logIn", value: "show"})}>Se connecter</Button>
                    </Col>
                </Row>
                <div className="position-absolute down w-100 d-flex justify-content-center">
                    <Link className="text-white"><ChevronDoubleDown/></Link>
                </div>
            </Container>
            <Container className="my-5 py-5">
                <div className="bg-default box-shadow text-white position-relative px-5">
                    <div className="position-absolute fleche w-100 d-flex">
                        <div className="angle"></div>
                        <p className="bg-dark py-2 pl-5">DIARIKO</p>
                    </div>
                    <p className="text-justify pt-5 mt-3">{presentation_text}</p>
                    <div className="d-flex justify-content-end pr-5">
                        <Button variant="dark" className="presentation rounded-bottom-0 py-2">
                            <Play className="mr-2" size={30}/> Présentation Video
                        </Button>
                    </div>
                </div>
            </Container>
            <Container>
                <Row>
                    <Col lg={6}><ImageCard image={health} icon={healthIcon} title={"SANTE"} text={health_text} xpos={"end"} page="Health"/></Col>
                    <Col lg={6}><ImageCard image={study} icon={studyIcon} title={"ETUDE"} text={study_text} xpos={"end"} page="Study"/></Col>
                </Row>
                <Row className="mb-5">
                    <Col lg={6}><ImageCard image={work} icon={workIcon} title={"EMPLOI"} text={work_text} xpos={"start"} page="Work"/></Col>
                    <Col lg={6}><ImageCard image={family} icon={familyIcon} title={"FAMILLE"} text={family_text} xpos={"start"} page="Family"/></Col>
                </Row>
            </Container>
        </Container>
    )
}

export default HomeNotConnected
