import { FormControl, InputGroup } from "react-bootstrap"
import { Search } from 'react-bootstrap-icons'
const SearchBar = ({container,bar,icon,placeholder}) => {
    return (
            <InputGroup className={"mx-5 " + container}>
                <InputGroup.Prepend>
                    <InputGroup.Text className={"bg-1 border-0 " + icon}><Search/></InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl placeholder={placeholder} className={"border-0 bg-1 " + bar}/>
            </InputGroup>
    )
}

export default SearchBar
